
import React, {useState, useEffect} from 'react';

const ReadMore = props =>{
    const [charLimit, setCharLimit] = useState(150);

    useEffect(() => {
        setCharLimit(props.charLimit);
    }, [props.charLimit]);

    
    const getMore = () =>{
        const showText = () =>{
            setCharLimit(props.children.length);
            return 1;
        }
        
        const hideText = () =>{
            setCharLimit(props.charLimit);
            return 1;
        }

        const textLimit = charLimit;
        if(typeof(props.children.length) != 'undefined'){
            if(props.children.length > textLimit){
                let smallString = props.children.substr(0, textLimit)+'... ';
                return <span className='small-string'>{smallString}<button aria-label='read more' className='read read-more' style={{padding: '0', margin: '0', border: 'none', backgroundColor: 'none'}} onClick={showText}>read more</button></span>
            }
            else if(props.children.length < textLimit){
                return <span className='short-text'>{props.children}</span>
            }
        }
        return(
            <span className='long-text'>{props.children+' '}<button aria-label='read less' className='read read-less' style={{padding: '0', margin: '0', border: 'none', backgroundColor: 'none'}} onClick={hideText}>read less</button></span>
        )
    }

    return(
    <div>{getMore()}</div>
    )
}

export default ReadMore;