import React from 'react';
import styled from 'styled-components/macro';
import {graphql} from 'gatsby';
import { getSection } from '../paragraphHelpers';
import breakpoint from '../styles/breakpoints';

const IconWrapper = styled.div`
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;

    padding-bottom: ${({paddingBottom}) => paddingBottom};
    padding-top: ${({paddingTop}) => paddingTop};

    @media screen and ${breakpoint.device.sm}{
        padding-top: 60px;
        padding-bottom: 60px;
    }
	@media screen and ${breakpoint.device.xs}{
        padding-top: 45px;
        padding-bottom: 45px;
    }

    .icon{
        &:not(::nth-child(4n)){
            padding-left: 24px;
        }
    }
    @media screen and ${breakpoint.device.sm}{
        padding-top: 80px;
        padding-bottom: 80px;
    }

    @media screen and ${breakpoint.device.xs}{
        padding-top: 50px;
        padding-bottom: 50px;
    }
`;



const IconContainer = ({node}) => {
    let icons = node.relationships.paragraphz.map(getSection);

    return(
        <IconWrapper paddingTop={node.field_padding_top} paddingBottom={node.field_padding_bottom}>
            {icons}
        </IconWrapper>
    )
}

export default IconContainer;

export const fragment = graphql`
    fragment IconContainer on paragraph__icon_container{
        id
        field_padding_bottom
        field_padding_top
        relationships{
            paragraphz: field_icons{
                type: __typename
                ...Icons
            }
        }
    }
`;