import React, { useState } from 'react';
import { graphql } from "gatsby";
import styled from 'styled-components/macro';
import { ParagraphParser } from '../paragraphHelpers';
import breakpoint from '../styles/breakpoints';
import UAColors from 'ua-color-name';

const SectionStyled = styled.section`
	display: block;
	padding-top: ${({ paddingTop }) => paddingTop};
	padding-bottom: ${({ paddingBottom }) => paddingBottom};

	@media screen and ${breakpoint.device.sm}{
    padding-top: 60px;
    padding-bottom: 60px;
  }
	
	@media screen and ${breakpoint.device.xs}{
		padding-top: 45px;
		padding-bottom: 45px;
  }
`;


const TabWrapper = styled.div`
	display: block;
	position: relative;

	&.blockTabs{
		.tabsContent{
			background-color: white;
		}
	}

	&.bubbleTabs{
		.tabsContent{
			padding: 40px 0 0;
		}
	}
`;


const Tabs = styled.div`
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
`;


const Tab = styled.button`
	display: block;
	background-color: ${({ bttnColor }) => UAColors(bttnColor)};
	color: white;
	border: none;
	font: normal normal bold 22px/27px Proxima Nova Condensed;
	margin-right: 15px;
	padding: 10px 60px;
	max-width: 347px;

	&:last-child{
		margin-right: 0;
	}

	&.blockTabs{
		border-radius: 5px 5px 0px 0px;
		@media screen and ${breakpoint.device.sm}{
			width: 100%;
			max-width: 100%;
			border-radius: 0;
			margin: 0;
			border-bottom: 2px solid ${UAColors('coolgray')}
		}

	}

	&.bubbleTabs{
		border-radius: 40px;
		width: 347px;
		margin-bottom: 20px;
		background-color: transparent;
		color: ${({ bttnColor }) => UAColors(bttnColor)};
	}

	${({ active, bttnColor }) => active && `
		&.blockTabs{
			background-color: white;
			color: ${UAColors(bttnColor)};
		}
		&.bubbleTabs{
			background-color: ${UAColors(bttnColor)};
			color: white;
		}
	`}

	${({ leftAlign }) => leftAlign && `
		padding: 10px 60px 10px 30px;
		text-align:left;
	`}

`;


const TabsContent = styled.div`
	display: block;
	padding: 60px;
`;


const Content  = styled.div`
	${props => (props.active ? "" : "display:none")}
`;


const TabContent = ({ node }) => {
	const tabs = node.relationships.tab,
				bttnColor = node.field_background_color,
				[active, setActive] = useState(tabs[0]);

	return (
		<SectionStyled paddingTop={node.field_top_padding} paddingBottom={node.field_bottom_padding}>
			<TabWrapper className={`${node.field_tab_style === "Style 1" ? 'blockTabs ' : 'bubbleTabs'} tabWrapper`} >
				<Tabs role="tablist" className="tabs">
					{
						tabs.map(tab => 
							<Tab 
								key={`${tab.id}-tab`} 
								id={`${tab.title.replace(' ', '-').toLowerCase()}-tab`}
								role="tab" 
								aria-controls={tab.title.replace(' ', '').toLowerCase()} 
								onClick={() => setActive(tab)} 
								className={`${node.field_tab_style === "Style 1" ? 'blockTabs ' : 'bubbleTabs'} tabElm`}
								data-toggle="tab"
								active={active === tab} 
								bttnColor={bttnColor}
								leftAlign={node.leftAlign}
							>
								{tab.title}
							</Tab> 
						)
					}
				</Tabs>
				<TabsContent className="tabsContent">
					{
						tabs.map(tab => 
							<Content 
								key={tab.id}
								id={tab.title.replace(' ', '').toLowerCase()}
								role="tabpanel"
								aria-labelledby={`${tab.title.replace(' ', '-').toLowerCase()}-tab`}
								className="tabContent"
								active={active === tab}
							>
								{ParagraphParser(tab.field_az_text_area.processed)}
							</Content>
						)
					}
				</TabsContent>
			</TabWrapper>
		</SectionStyled>
	)
}

export default TabContent;

export const fragment = graphql`
	fragment TabContent on paragraph__content_with_tabs {
		id
		field_top_padding
		field_bottom_padding
		field_tab_style
		field_background_color
		leftAlign: field_left_align_tab_title
		relationships {
			tab: field_tab {
				id
				title: field_tab_title
				field_az_text_area {
					processed
					value
				}
			}
		}
	}
`;
