import React from 'react';
import { graphql } from 'gatsby'
import Card from '../components/Card';

const CardContainer = ({node}) =>{
    return(
        <Card.Container href={node.field_card_path.uri} rel='noopener'>
            <Card>
                <Card.Title>{node.field_card_title}</Card.Title>
                <Card.Group>
                    <Card.Description>{node.field_card_description}</Card.Description>
                    <Card.ClickIndicator />
                </Card.Group>
            </Card>
        </Card.Container>
    );
}

export default CardContainer;

export const fragment = graphql`
    fragment CardContainer on paragraph__card{
        field_card_path {
            title
            uri
        }
        field_card_title
        field_card_description
        id
    }
`;