import React from "react";
import styled from 'styled-components/macro';
import { graphql } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import breakpoint from '../styles/breakpoints';

const ContentStyled = styled.div`
    display: flex;
    justify-content: space-between;

    position: relative;

    padding-top: ${({ margintop }) => margintop};
    padding-bottom: ${({ bottomMargin }) => bottomMargin};

    ${({reverse}) => reverse && `
        align-items: lex-start;
        ${ContentImageWrap}{
            order: 1;
        }
        ${ContentGroup}{
            order: 2;
        }
    `}

    a{
        font-weight: 700;
        &.btn{
            font-size: 18px;
            span{
                font-size: 18px;
            }
        }
    }

    @media screen and ${breakpoint.device.sm}{
        padding-top: 60px;
        padding-bottom: 60px;
        text-align: left;
    }

    @media screen and ${breakpoint.device.xs}{
		flex-direction: column;
        justify-content: center;
        padding-top: 45px;
        padding-bottom: 45px;

        ${({reverse}) => reverse && `
            ${ContentImageWrap}{
                margin-bottom: 40px;
            }
        `}

        ${({above}) => above && `
            ${ContentGroup}{
                order: 2;
            }
            ${ContentImageWrap}{
                order: 1;
                margin-top: 0;
                margin-bottom: 40px;
            }
        `};
    }
`;

const ContentImage = styled(GatsbyImage)`
    display: block;

    border-radius: 18px;

    width: 348px;
`;

const ContentGroup = styled.div`
    display: block;
    width: calc(100% - 382px);
    max-width: 729px;

    ${({ hasImage }) => hasImage && `
        max-width: 529px;
    `};

    @media screen and ${breakpoint.device.xs}{
        width: 100%;
        max-width: initial;
        margin-bottom: 9px;
    }
`;

const ContentTitle = styled.h2`
    font-size: var(--h2-font-size);
    font-weight: 900;

    max-width: 434px;
    margin-bottom: 30px;
`;

const ContentText = styled.p`
    font-size: var(--h2-font-size);
    a{
        max-width: 500px;
    }
`;

const ContentImageWrap = styled.div`
    ${({ above }) => above && `
        position: absolute;
        transform: translateY(-50%);
        right: 0;
        top: -80px;
        z-index: 999;
    `};

    ${({ below }) => below && `
        position: absolute;
        transform: translateY(50%);
        right: 0;
        bottom: 0px;
        z-index: 999;
    `};

    ${({double, above}) => double && above && `
        transform: translateY(-136px);
    `};

    ${({double, below}) => double && below && `
        transform: translateY(136px);
    `};

    ${({double}) => double && `
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        ${ContentImage}:first-child{
        order: 2;
        }

        ${ContentImage}:last-child{
            order: 1;
            margin-bottom: 40px;
        }

        ${({ below }) => below && `
            transform: translateY(136px) !important;
        `};

        @media screen and ${breakpoint.device.xs}{
            ${ContentImage}:last-child{
                display: none;
            }
        }
    `};

    @media screen and ${breakpoint.device.xs}{
        margin-top: 30px;
        ${({ above }) => above && `
            position: relative;
            transform: translateY(0);
            top: 0;
        `};

        ${({ below }) => below && `
            position: relative;
            transform: translateY(0);
            bottom: 0
        `};
    }
`;

const ParagraphContent = ({ node }) => {
    let title = node.content_title || null;
    let copy = node.field_content_text || null;
    let img = node.relationships.field_content_image || null;
    let sec_img = node.relationships.field_content_second_image || null;
    let reverse = node.field_content_reverse || false;
    let center = true,
        above = false,
        below = false;

    let narrow = false;

    if (img || node.field_make_narrow) {
        narrow = true;
    }

    if (img != null) {
        img = getImage(node.relationships.field_content_image.localFile) || null;
    }
    if (sec_img != null) {
        sec_img = getImage(node.relationships.field_content_second_image.localFile) || null;
    }

    if (node.field_content_image_position !== 'center') {
        center = false;
        if (node.field_content_image_position === 'above') {
            above = true;
        } else {
            below = true;
        }
    }

    if(reverse){
        center = true;
        above = false;
        below = false;
    }

    return (
        <ContentStyled margintop={node.field_top_padding} bottomMargin={node.field_bottom_padding} reverse={reverse} above={above}>
            <ContentGroup hasImage={narrow}>
                {title != null &&
                    <ContentTitle dangerouslySetInnerHTML={{ __html: title.processed }} />
                }
                {copy != null &&
                    <ContentText dangerouslySetInnerHTML={{ __html: node.field_content_text.processed }} />
                }
            </ContentGroup>
            <ContentImageWrap double={sec_img != null ? true : false} center={center} above={above} below={below}>
            { img != null &&
                    <ContentImage alt={img.alt} image={img} />
            }
            {sec_img != null &&
                <ContentImage alt={node.second_img.alt} image={sec_img} />
            }
            </ContentImageWrap>
        </ContentStyled>
    );
}

export default ParagraphContent;


export const fragment = graphql`
    fragment Content on paragraph__content{
        id
        field_content_image_position
        field_top_padding
        field_bottom_padding
        field_make_narrow
        field_content_reverse
        img: field_content_image{
            alt
            height
            title
            width
        }
        second_img: field_content_second_image{
            alt
            height
            title
            width
        }
        content_title: field_content_title{
            format
            processed
            value
        }
        field_content_text{
            format
            processed
            value
        }
        relationships{
            field_content_image{
                id
                localFile{
                    childImageSharp{
                        gatsbyImageData(
                            placeholder: BLURRED
                            width: 348
                            height: 400
                            layout: CONSTRAINED
                        )
                    }
                }
            }
            field_content_second_image{
                id
                localFile{
                    childImageSharp{
                        gatsbyImageData(
                            placeholder: BLURRED
                            width: 348
                            height: 280
                            layout: CONSTRAINED
                        )
                    }
                }
            }
        }
    }

`;
