import React from "react";
import { graphql } from "gatsby";
import { Link } from ".";

const Button = ({ node }) => {
	let bttnStyle = node.relationships.field_button_style.name;

	return (
		<Link
      primary={(bttnStyle==='primary') ? 'primary' : undefined}
      secondary={(bttnStyle==='secondary') ? 'secondary' : undefined}
      margintop={node.field_top_padding}
      marginbottom={node.field_bottom_padding}
      to={node.link.uri}>
          {node.link.title}
    </Link>
	)
}

export default Button;

export const fragment = graphql`
	fragment Button on paragraph__button {
		id
		field_top_padding
		field_bottom_padding
		link: field_link {
			title
			uri
		}
		relationships{
			field_button_style{
				name
			}
		}
	}
`;
