import React from 'react';
import UAColors from 'ua-color-name';
import styled, { css } from 'styled-components/macro';
import { graphql } from 'gatsby';
import { getSection } from '../paragraphHelpers';

const SectionStyled = styled.section`
	display: block;
	background-color: ${({ bgColor }) => UAColors(bgColor)};
	z-index: -1;
`;

const SectionContainer = styled.div`
	display: block;
	max-width: 1180px;
	width: 100%;
	margin: auto;
	padding: 0 24px;
	box-sizing: border-box;

	${({ hero }) =>
		hero &&
		css`
			max-width: 100vw;
			padding: 0;
        `}
`;




const Section = ({ node }) => {
	let paragraphs = node.relationships.paragraphs.map(getSection),
		bgColor = node.field_hero_background_colo,
		hero = node.field_hero_section ? 'hero' : null;


	return (
		<SectionStyled id={hero} hero={node.field_hero_section} bgColor={bgColor} pageTitle={node.pageTitle} >
			<SectionContainer hero={node.field_hero_section}>
				{paragraphs}
			</SectionContainer>
		</SectionStyled>
	)
};

export default Section;

export const fragment = graphql`
	fragment Section on paragraph__section {
		id
		field_hero_background_colo
		field_hero_section
		relationships{
			paragraphs: field_section_paragraphs {
				type: __typename
				...Hero
				...ColumnsOfButtons
				...CardGrid
				...StatsDivider
				...Content
				...TwoColumnContent
				...Gigg
				...IconContainer
				...CalendarCards
				...Divider
				...DeadlinesTable
				...TabContent
			}
		}
	}
`;