import React from 'react';
import {graphql} from 'gatsby';
import Icon from './Icon/index';
import {getImage} from 'gatsby-plugin-image';

const Icons = ({node}) => {
    let title = node.field_icon_title || null;
    let img = node.relationships.field_icon_image|| null;
    let desc = node.field_icon_desc || null;

    if(img != null){
        img = getImage(img.localFile) || null;
    }

    return(
        <Icon image={img} alt={node.field_icon_image.alt} title={title.value} desc={desc.value} />
    )
}

export default Icons;

export const fragment = graphql`
    fragment Icons on paragraph__single_icon{
        id
        field_icon_desc {
            processed
            value
        }
        field_icon_image {
            alt
        }
        field_icon_title {
            processed
            value
        }
        relationships{
            field_icon_image{
                localFile{
                    childImageSharp{
                        gatsbyImageData(
                            placeholder: BLURRED
                            width: 127
                            height: 95
                            layout: CONSTRAINED
                        )
                    }
                }
            }
        }
    }
`;