import React, {useState, useEffect} from 'react';
import { graphql, useStaticQuery } from "gatsby"
import UAColors from 'ua-color-name';
import styled from 'styled-components/macro';


const RFIwrapper = styled.div`
	display: block;
	#mainRFI{
		.rfiDropDown{
			&.active{
				display: block;
			}
			&.inactive{
				display: none;
			}
			label{
				color: ${UAColors('arizonablue')};
				font-weight: bold;
				display: block;
			}
			select{
				padding: 12px 14px;
			}
			margin-bottom: 20px;
		}
	}
`;

const Forms = styled.div`
	display: block;
	div.slateForm{
		&.active{
			display:block;
		}
		&.inactive{
			display: none;
		}
	}
`;


const RFI = () => {
	const [formArr, setFormArr] = useState([]);
	const [dispForm, setDispForm] = useState();
	const [isGlobal, setGlobal] = useState(false);

	const allRFI = useStaticQuery(graphql`{
		allTaxonomyTermAdmissionsRfi {
			nodes {
				id
				field_rfi_filters
				field_slate_embed_src
			}
		}
	}`);

	let rfis = allRFI.allTaxonomyTermAdmissionsRfi.nodes


	/*
		SelectionChanged(num, e) main controller function should run anytime 
		slection option is changed. takes parameters num, which is an integer representing
		the select tag currently being changed, e, which is the html ojbect.
			1. ResetForm(num).
			2. Update formArr state with current select value.
			3. Update isGlobal state (modifies options in select elem "Global" vs "Campus Near You").
			4. checkForForm().
			5. initForm().
	*/
	const SelectionChanged = (num, e) => {
		//clear previous selections
		ResetForm(num);
		//update form array with newly selected value.
		let tempFormArr = formArr.slice();
		tempFormArr[num] = e.currentTarget.value;
		setFormArr(tempFormArr)

		let allFormsToDisp = document.getElementsByClassName('slateForm');
		for(let i = 0; i < allFormsToDisp.length; i++){
			allFormsToDisp[i].classList.remove('active');
			allFormsToDisp[i].classList.add('inactive');
		}
		

		// NEED SOME SHIT LOGIC HERE TO DISPLAY DIFFERENT DROPDOWNS

		// update select option global vs cny, based on freshman or transfer
		if(tempFormArr[1] === "freshmen"){
			setGlobal(true);
		}else{
			setGlobal(false);
		}

		//check if form should be displayed
		let isFormDisplayed = checkForForm(tempFormArr);
		if(isFormDisplayed){
			initForm(isFormDisplayed)
		}
	}


	/*
		initForm(src) 
	*/
	const initForm = (currSrc) =>{
		let formID = currSrc.split("div=")[1].split("&sys")[0];
		setDispForm(formID);
		// stolen from Slate embed code
		let script = document.createElement('script');
		script.async = 1;
		script.src = currSrc + ((window.location.search.length > 1) ? '&' + window.location.search.substring(1) : '');
		let s = document.getElementsByTagName('script')[0];
		s.parentNode.insertBefore(script, s);
		// stolen from Slate embed code
	}


	/*
		checkForForm() joins formArr on | to make a string, 
		loops through rfis to check if object has a matching field_rfi_filters property
		if match the src string is returned else false.
	*/
	const checkForForm = (tempFormArr) => {
				//create temporary array of exact length (no empty strings)
		let tempArr = tempFormArr.filter((e)=>{ return e.trim() != ''; }),
				formKey = tempArr.join('|').toLowerCase(),
				retEmbed = false;

		//loop through rfis and check for match on string
		// if match update retEmbed to value of embeded form
		rfis.forEach(rfi => {
			if(formKey === rfi.field_rfi_filters) {
				retEmbed = rfi.field_slate_embed_src;
			}
		});

		return retEmbed;
	}


	/*
		ResetForm(num) takes an int which represents
		the current select field being modified, Loops
		through remaining select fields (num+1 ++) setting to 
		default value and hiding any displayed forms. 
	*/
	const ResetForm = (num) => {
		// all others selects and current displayed form if any
		let slctTags = document.getElementById('mainRFI').getElementsByTagName('select');
		for(let i = num+1; i < slctTags.length; i++){
			slctTags[i].selectedIndex = 0;
			formArr[i] = '';
		}
	}

	return (
		<RFIwrapper>
			<form id="mainRFI">
				<div className="active rfiDropDown">
  				<label htmlFor="degreeType">What type of degree are you seeking?</label>
					<select id="degreeType" autoComplete="off" onChange={e => SelectionChanged(0, e)}>
						<option value=" ">- Select -</option>
						<option value="undergrad">Undergraduate</option>
						<option value="grad">Graduate</option>
					</select>
				</div>

				<div className={`${formArr[0] === 'undergrad' ? 'active' : 'inactive'} rfiDropDown`}>
					<label htmlFor="studentType">What type of student will you be when you enroll?</label>
					<select id="studentType" autoComplete="off" onChange={e => SelectionChanged(1, e)}>
						<option value=" ">- Select -</option>
						<option value="freshmen">Freshman</option>
						<option value="transfer">Transfer</option>
					</select>
				</div>
				
				<div className={`${formArr[0] === 'grad' || formArr[1] === 'freshmen' || formArr[1] === 'transfer' ? 'active': 'inactive'} rfiDropDown`}>
					<label htmlFor="campusLoc">Which University of Arizona campus do you plan to attend?</label>
					<select id="campusLoc" autoComplete="off" onChange={e => SelectionChanged(2, e)}>
						<option value=" ">- Select -</option>
						<option value="tucson">Tucson Campus</option>
						<option value={isGlobal ? 'global': 'cny'}>{isGlobal ? 'Global Location': 'A Campus Near You'}</option>
						<option value="online">Online</option>
					</select>
				</div>

				<div className={`${formArr[2] === 'cny' && formArr[0] !== 'grad' ? 'active': 'inactive'} rfiDropDown`}>
					<label htmlFor="campusNearYou">A Campus Near You</label>
					<select id="campusNearYou" autoComplete="off" onChange={e => SelectionChanged(3, e)}>
						<option value=" ">- Select -</option>
						<option value="us">United States</option>
						<option value="international">International</option>
					</select>
				</div>
				
				<Forms>
					{rfis.map(rfi => 
						<div 
							key={rfi.id}
							className={`${dispForm === rfi.field_slate_embed_src.split("div=")[1].split("&sys")[0] ? 'active' : 'inactive'} slateForm`}
							id={rfi.field_slate_embed_src.split("div=")[1].split("&sys")[0]}>Loading...</div>
					)}
				</Forms>
			</form>
		</RFIwrapper>
	)
}

export default RFI;