import React from "react";
import ReactHtmlParser from 'react-html-parser';
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { graphql, Link, useStaticQuery } from "gatsby";
// Containers
import Section from "./containers/Section";
import CardContainer from './containers/Card';
// Components
import HtmlField from "./components/HtmlField";
import Button from "./components/Button";
import ButtonColumn from "./components/ButtonColumn";
import ColumnsOfButtons from "./components/ColumnsOfButtons";
import CardGrid from "./components/CardGrid";
import StatsDivider from './components/StatsDivider';
import Content from './components/ParagraphContent';
import TwoColumnContent from './components/TwoColumnContent';
import Hero from './components/ParagraphHero';
import Gigg from './components/Gigg';
import SocialMediaLink from './components/SocialMediaLink';
import Icons from './components/Icons';
import IconContainer from './components/IconContainer';
import CalendarCards from './components/CalendarCards';
import Divider from './components/Divider';
import DeadlinesTable from './components/DeadlinesTable';
import TabContent from './components/TabContent';
import RFI from './components/RFI';

const components = {
	paragraph__section: Section,
	paragraph__card: CardContainer,
	paragraph__html_field: HtmlField,
	paragraph__button: Button,
	paragraph__button_column: ButtonColumn,
	paragraph__columns_of_buttons: ColumnsOfButtons,
	paragraph__card_grid: CardGrid,
	paragraph__content: Content,
	paragraph__divider: Divider,
	paragraph__stats_divider: StatsDivider,
	paragraph__two_column_content: TwoColumnContent,
	paragraph__gigg_board: Gigg,
	paragraph__admissions_hero: Hero,
	paragraph__social_media_link: SocialMediaLink,
	paragraph__single_icon: Icons,
	paragraph__icon_container: IconContainer,
	paragraph__calendar_cards: CalendarCards,
	paragraph__deadline_table: DeadlinesTable,
	paragraph__content_with_tabs: TabContent,
	paragraph__include_rfi_dropdown_: RFI
};


export const getSection = node => {
    if (components.hasOwnProperty(node.type)) {
        const SectionComponent = components[node.type];
        return <SectionComponent key={node.id + Math.floor(Math.random() * 256)} node={node} pageTitle={node.pageTitle} />;
    }
    return <p key={node.id}>Unknown type {node.type}{node.__typename}</p>;
};


// Parses content converting embedded images into GatsbyImage(s)
// and adding target="_blank" rel="noopner" to external anchors
// if internal anchor then converts to <Link>
export function ParagraphParser(processedData) {
	const allImages = useStaticQuery(graphql`{
		allFileFile {
			edges {
				node {
					drupal_internal__fid
					localFile {
						childImageSharp {
							gatsbyImageData(
								placeholder: BLURRED
								formats: [AUTO, WEBP, AVIF]
							)
						}
					}
				}
			}
		}
	}`);

	let mediaID = '';
	if (processedData.includes("data-media-source-value")) {
		mediaID = parseInt(processedData.match(/data-media-source-value="(\d*)"/)[1]);
	}
	let parsed = new ReactHtmlParser(processedData, {
			transform: function transform(node){
				if(node.type === "tag" && node.name === "img"){
					for (let i = 0; i < allImages.allFileFile.edges.length; i++) {
						// if current file iteration has a drupal_internal__fid id which matches our file name and is a local file
						if(mediaID && allImages.allFileFile.edges[i].node.drupal_internal__fid === mediaID && allImages.allFileFile.edges[i].node.localFile){
							//convert node to GatsbyImage
							let img = getImage(allImages.allFileFile.edges[i].node.localFile);
							img.alt = node.attribs['alt']
							return <GatsbyImage key={img.images.sources[0].type + Math.floor(Math.random() * 256)} image={img} alt={img.alt} style={{width: "auto"}}/>
						}
					}
				}
				// if node is an anchor
				if(node.type === "tag" && node.name === "a"){
					let protoRegEx = new RegExp('^(?:[a-z]+:)?//', 'i'),
							url = node.attribs.href;
					if(protoRegEx.test(url)){
						// if passes url must have // in it
						if(url.split("//")[1].split(".")[0].toString().toLowerCase() !== 'arizona'){
							return <a key={node.attribs.href + Math.floor(Math.random() * 256)} href={url} target="_blank" rel="noopener" className={node.attribs.class}>{node.children[0].data}</a>
						}
					}
					return <Link key={node.attribs.href + Math.floor(Math.random() * 256)} className={node.attribs.class} to={url}>{node.children[0].data}</Link>
				}
			}
		});
	return parsed;
}