import React from 'react';
import UAColors from 'ua-color-name';
import { graphql } from 'gatsby';
import { getSection } from "../paragraphHelpers";
import styled from 'styled-components/macro';
import breakpoint from '../styles/breakpoints';
import GiggBoard from './GiggBoard';

const SectionStyled = styled.section`
	display: block;
	padding-top: ${({ paddingTop }) => paddingTop};
    padding-bottom: ${({ paddingBottom }) => paddingBottom};

	@media screen and ${breakpoint.device.sm}{
        padding-top: 60px;
        padding-bottom: 60px;
    }
	@media screen and ${breakpoint.device.xs}{
        padding-top: 45px;
        padding-bottom: 45px;
    }
`;

const ContentArea = styled.div`
	position; relative;
	margin-bottom: 60px;
	h2{
		color: ${UAColors('arizonablue')};
		margin-bottom: 30px;
	}
	&.carouselLayout{
		.textSocialWrap{
			display: flex;
			flex-direction: row;
			flex-wrap: nowrap;
			justify-content: space-between;
			.textContentArea{
				max-width: 570px;
				width: 100%;
			}
			.socialMedias {
				max-width: 460px;
				width: 100%;
				min-height: 100%;
  				display: flex;
  				flex-wrap: wrap;
  				flex-direction: row;
				justify-content: space-between;
				a{
					display: flex;
					flex-direction: row;
					flex-wrap: nowrap;
					align-items: center;
					justify-content: space-between;
				}
			}
		}
		@media screen and ${breakpoint.device.sm}{
			.textSocialWrap{
				display: block;
				.socialMedias {
					margin-top: 40px;
					> a {
						margin-bottom: 20px;
					}
				}
			}
		}
	}
	&.gridLayout{
		display: block;
		h2{
			text-align: center;
		}
		.textSocialWrap{
			text-align: center;
			max-width: 800px;
			margin: 0 auto;
			.textContentArea{
				margin-bottom: 40px;
			}
			.socialMedias{
				display: flex;
				flex-wrap: wrap;
				max-width: 800px;
				width: 100%;
				margin: 0 auto;
				justify-content: space-between;
			}
		}
	}
`;


const SocialMediaContainer = styled.div`
	display: block;
	position: relative;
`;

const Gigg = ({ node }) => {
	let isCarousel = node.field_board_display_type === 'carousel';
	return (
		<SectionStyled paddingTop={node.field_top_padding} paddingBottom={node.field_bottom_padding}>
			<ContentArea className={`${isCarousel ? 'carouselLayout ' : 'gridLayout'}`}>
				<h2>{node.field_gigg_title}</h2>
				<div className="textSocialWrap">
					<div className="textContentArea" dangerouslySetInnerHTML={{ __html: node.field_gigg_content.processed }} />
					<SocialMediaContainer className="socialMedias">
						{node.relationships.socialMedias.map((getSection))}
					</SocialMediaContainer>
				</div>
			</ContentArea>
			<GiggBoard accountId={node.field_gigg_account_id} folderId={node.field_gigg_folder_id} displayType={node.field_board_display_type} />
		</SectionStyled>
	)
}

export default Gigg;

export const fragment = graphql`
    fragment Gigg on paragraph__gigg_board{
		id
		field_gigg_title
		field_top_padding
        field_bottom_padding
		field_gigg_content {
			processed
		}
		field_gigg_account_id
		field_gigg_folder_id
		field_board_display_type
		relationships {
			socialMedias: field_social_media_links {
				type: __typename
				...SocialMediaLink
			}
		}
	}
`;