import React from 'react';
import styled from 'styled-components/macro';
import { ParagraphParser } from '../paragraphHelpers';
import { graphql } from "gatsby";
import breakpoint from '../styles/breakpoints';
import UAColors from 'ua-color-name';

const SectionStyled = styled.section`
	display: block;
	padding-top: ${({ paddingTop }) => paddingTop};
	padding-bottom: ${({ paddingBottom }) => paddingBottom};

	${({ borderTop }) => borderTop && `
		border-top: 1px solid ${UAColors('silver')};
	`}

	${({ borderBottom }) => borderBottom && `
		border-bottom: 1px solid ${UAColors('silver')};
	`}

	@media screen and ${breakpoint.device.sm}{
    padding-top: 60px;
    padding-bottom: 60px;
  }
	@media screen and ${breakpoint.device.xs}{
		padding-top: 45px;
		padding-bottom: 45px;
  }
`;

const ContentTitle = styled.h2`
  font-size: var(--h2-font-size);
  font-weight: 900;
  max-width: 434px;
  margin-bottom: 30px;
`;

const TwoColContainer = styled.div`
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: ${({ align }) => align};
	flex-direction: row;
	flex-wrap: wrap;
	.col{
		display: block;
		position: relative;
		width: 100%;
		text-align: left;
		a{
			color: var(--uared);
			font-weight: 700;
		}
		.btn{
			max-width: none;
    	width: fit-content;
    	padding: 12px 50px;
		}
		&:last-child{
			margin-bottom: 0;
			@media screen and ${breakpoint.device.sm}{
				margin-top: 40px;
				.gatsby-image-wrapper{
					clear: both;
					float: none;
				}
			}
		}
		.gatsby-image-wrapper{
			${'' /* float: left; */}
			@media screen and ${breakpoint.device.sm}{
				clear: both;
				float: none;
			}
			> div {
				display: none !important;
			}
			img[data-placeholder-image]{
				display: none !important;
			}
		}
	}
`;
const LeftCol = styled.div`
	display: block;
	position: relative;
	max-width: ${({ leftWidth }) => leftWidth};
	box-sizing: border-box;
	padding-right: calc(109px / 2);

	${({ hide }) => hide && `
		&:after{
			display: none;
		}
	`}
	&:after{
		content: '';
		background-color: var(--silver);
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		width: 1px;
	}
	img{
		display: block;
		position: relative;
		height: auto;
		border-radius: 24px;
		max-width: 100%;
	}
	@media screen and ${breakpoint.device.sm}{
		max-width: 50%;
    }
	@media screen and ${breakpoint.device.xs}{
		max-width: 100%;
		padding-right: 0;
		&:after{
			display: none;
		}
    }
	`;
const RightCol = styled.div`
	display: block;
	position: relative;
	max-width: ${({ leftWidth }) => leftWidth};
	padding-left: calc(109px / 2);
	box-sizing: border-box;
	img{
		border-radius: 24px;
		display: block;
		position: relative;
		width: 100%;
		height: auto;
	}
	@media screen and ${breakpoint.device.sm}{
		max-width: 50%;
    }
	@media screen and ${breakpoint.device.xs}{
		max-width: 100%;
		padding-left: 0;
		margin-top: 18px;
    }
`;

const TwoColumnContent = ({ node }) => {
	let title = node.field_content_title || null,
			leftContent = ParagraphParser(node.field_left_column.processed),
			rightContent = ParagraphParser(node.field_right_column.processed),
			leftWidth = node.field_left_col_width + '%',
			hide = node.field_hide_line,
			align = node.field_align_content;
	
	return (
		<SectionStyled paddingTop={node.field_top_padding} paddingBottom={node.field_bottom_padding} borderTop={node.field_border_top} borderBottom={node.field_border_bottom}>
			{title != null &&
				<ContentTitle dangerouslySetInnerHTML={{ __html: title.processed }} />
			}
			<TwoColContainer align={align}>
				<LeftCol hide={hide} leftWidth={leftWidth} className='col'>{leftContent}</LeftCol>
				<RightCol leftWidth={'calc(100% - ' + leftWidth + ')'} className='col'>{rightContent}</RightCol>
			</TwoColContainer>
		</SectionStyled>
	);
}

export default TwoColumnContent;

export const fragment = graphql`
		fragment TwoColumnContent on paragraph__two_column_content {
		field_top_padding
		field_bottom_padding
		field_left_col_width
		field_align_content
		field_border_bottom
		field_border_top
		field_hide_line
		field_content_title{
			processed
			value
		}
		field_left_column {
			processed
			value
		}
		field_right_column {
			processed
			value
		}
	}
`;

