import React from 'react';
import {graphql} from 'gatsby';
import {getSection} from '../paragraphHelpers';
import Card from '../components/Card';

const CardGrid = ({node}) => {
    let cards = node.relationships.paragraphss.map(getSection);
    let above = node.field_padding_top;
    let below = node.field_padding_bottom;

    return (
        <Card.Grid above={above} below={below}>
            {cards}
        </Card.Grid>
    )
};

export default CardGrid;

export const fragment = graphql`
    fragment CardGrid on paragraph__card_grid{
        field_padding_bottom
        field_padding_top
        relationships {
            paragraphss: field_cards{
                type: __typename
                ...CardContainer
            }
        }
        id
    }
`;