import React from 'react';
import SEO from '../components/SEO';
import { graphql, useStaticQuery } from 'gatsby';
import { getSection } from '../paragraphHelpers';
import '../web-components/az-main-menu';
import '../web-components/az-redbar';
import '../web-components/az-middle-header';

const Page = ({ data }) => {
	const page = data.nodeAdmissionsFlexiblePage,
		sections = page.relationships.sections.map(getSection);

	return (
        <>
			<SEO pageMeta={page.metatag}/>
			<az-redbar baseurl="http://dev-az-admissions.pantheonsite.io" menuId="resources"></az-redbar>
			<az-middle-header></az-middle-header>
			<gdwc-arizonaedu-menu-bar branding="Main Navigation" baseurl="http://dev-az-admissions.pantheonsite.io" menuId="main"></gdwc-arizonaedu-menu-bar>
			{sections}
		</>
	)
};

export const query = graphql`
	query($id: String!) {
		nodeAdmissionsFlexiblePage(id: { eq: $id }) {
			id
			title
			created
			revision_timestamp
			path {
				alias
			}
			metatag {
				tag
				attributes {
					content
					group
					href
					name
					rel
					schema_metatag
				}
			}
			relationships {
				sections: field_main_content {
					type: __typename
					...Section
				}
			}
		}
	}
`;

export default Page;