import React from "react";
import styled from "styled-components";
import UAColors from 'ua-color-name';
import { graphql } from "gatsby";
import { getSection } from "../paragraphHelpers";
import breakpoint from '../styles/breakpoints';


const ColumnContainer = styled.div`
	position: relative;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	column-gap: 32px;
    @media screen and ${breakpoint.device.xs}{
		display: block;
		column-gap: none;
		margin: auto;
		text-align: center;
    }
`;

const ColumnWrap = styled.div`
	padding-top: ${({ above }) => above};
	padding-bottom: ${({ below }) => below};
	text-align: center;
	h2{
		text-align:left;
		margin-bottom: 60px;
		max-width: 538px;
		color: ${UAColors('arizonablue')};
	}

	h2{
		text-align: left;
		margin-bottom: 30px;
	}

	@media screen and ${breakpoint.device.sm}{
        padding-top: 60px;
        padding-bottom: 60px;
    }

    @media screen and ${breakpoint.device.xs}{
        padding-top: 45px;
        padding-bottom: 45px;
    }
`;


const ColumnsOfButtons = ({ node }) => {
	let bttnCols = node.relationships.paragraphs.map(getSection) || null,
		heading = node.field_bttn_col_heading || '',
		introContent = node.content;

	let above = node.field_padding_top;
	let below = node.field_padding_bottom;
	return (
		<ColumnWrap above={above} below={below}>
			<h2>{heading}</h2>
			{introContent !== null ? ((`${<div dangerouslySetInnerHTML={{ __html: introContent.processed }} />}`)) : ''}
			<ColumnContainer>
				{bttnCols}
			</ColumnContainer>
		</ColumnWrap>
	)
};

export default ColumnsOfButtons;

export const fragment = graphql`
	fragment ColumnsOfButtons on paragraph__columns_of_buttons {
		id
		field_bttn_col_heading
		field_padding_bottom
    field_padding_top
		content: field_bttn_col_content {
			format
			processed
			value
		}
		relationships {
			paragraphs: field_button_column {
				type: __typename
				...ButtonColumn
			}
		}
	}
`;
