import React from "react"
import PropTypes from "prop-types"
import { Helmet } from "react-helmet"
import { useLocation } from "@reach/router"
import { useStaticQuery, graphql } from "gatsby"

const SEO = ({pageMeta}) => {
    const { pathname } = useLocation();
    const { site } = useStaticQuery(query);
    const defaultMeta = site.siteMetadata;

    let seo = {
        'title': defaultMeta.defaultTitle || null,
        'description': defaultMeta.defaultDescription || null,
        'abstract': defaultMeta.abstract || null,
        'image': defaultMeta.siteUrl + defaultMeta.defaultImage || null,
        'url': defaultMeta.siteUrl || null,
        'twitter:summary': defaultMeta.twitterSummary || null,
        'twitter:description': defaultMeta.twitterDescription || null,
        'twitter:title': defaultMeta.twitterTitle || null,
        'twitter:url': defaultMeta.twitterUrl || null,
        'twitter:alt': defaultMeta.twitterAlt || null,
        'twitter:image': defaultMeta.twitterImage || null
    }

    // console.log(seo);
    // console.log(Object.keys(seo));
    // console.log(pageMeta);

    pageMeta.forEach(meta => {
        let cur = meta.attributes;
        if(cur.content != null){
            seo[`${cur.name}`] = cur.content
        }
    });

    return (
        <Helmet title={seo.title} description={seo.description}>
            <meta name="description" content={seo.description} />
            {seo.image && <meta name="image" content={seo.image} />}
            {seo.url && <meta property="og:url" content={seo.url} />}
            {seo.title && <meta property="og:title" content={seo.title} />}
            {seo.description && <meta property="og:description" content={seo.description} />}
            {seo["twitter:image"] && <meta name="twitter:card" content="summary_large_image" />}
            {seo["twitter:description"] && <meta name="twitter:description" content={seo.description} />}
            {seo["twitter:image"] && <meta name="twitter:image" content={seo.image} />}
        </Helmet>
    )
}

export default SEO

const query = graphql`
query SEO {
    site {
        siteMetadata {
            defaultTitle: title
            titleTemplate
            defaultDescription: description
            siteUrl: url
            defaultImage: image
        }
    }
}
`
