import React from 'react';
import styled from 'styled-components/macro'; import { graphql } from 'gatsby';
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import breakpoint from '../styles/breakpoints';


const StyledDivider = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;

    width: calc(100% - 48px);
    height: 400px;
    max-width: 1200px;


    ${({solo}) => (solo === 0) &&`
        max-width: 1600px;
        `}

    margin: auto;

    position: relative;
    z-index: 0;

    ${({ reverse }) => reverse && `
        .primary{
            order: 2;
            margin-right: 0 !important;
        }

        .secondary{
            order: 1;
            margin-right: 34px;
            margin-left: 0;
        }
    `};

    img{
        width: 100%;
        display: block;
    }

    &:before{
        content: '';

        position: absolute;
        left: calc(-1*((100vw - 100%)/2));
        top: 0;
        z-index: -1;

        width: 100vw;
        height: 50%;
        background-color: ${({ background }) => 'var(--' + background + ')'};
    }
`;

const DividerImg = styled(GatsbyImage)`
    display: block;

    width: 100%;
    height: 400px;
    max-width: 726px;

    border-radius: 24px;
    overflow: hidden;

    &.primary{
        margin-right: 34px;
        ${({solo}) => (solo === 0) &&`
            margin-right: 0;
            max-width: 100%;
        `};
    }

    &.secondary{
        max-width: 348px;
    }

    &.primary, &.secondary, &.tertiary{
        ${({solo}) => (solo === 2) &&`
            margin-right: 0px;
            max-width: 30%;
        `};
    }

    @media screen and ${breakpoint.device.xs}{
        &.primary{
            margin-right: 0;
        }
        &.secondary{
            display: none;
        }
    }
`;

const Divider = ({ node }) => {
    let solo = 0;
    let primeImg = node.relationships.field_divider_main_image || null,
        secImg = node.relationships.field_divider_secondary_image || null,
        terImg = node.relationships.field_divider_tertiary_image || null,
        altPrime = node.primary_img.alt;
    let bg = node.field_background_color;
    if (bg === 'coolgrey') {
        bg = 'coolgray';
    }
    let altSec = null;
    if (node.secondary_img != null) {
        altSec = node.secondary_img.alt;
    }
    let altTer = null;
    if (node.terImg != null) {
        altTer = node.terImg.alt;
    }

    if (primeImg != null) {
        primeImg = getImage(node.relationships.field_divider_main_image.localFile) || null;
    }

    if (secImg != null) {
        secImg = getImage(node.relationships.field_divider_secondary_image.localFile) || null;
        solo = 1;
    }
    
    if (terImg != null) {
        terImg = getImage(node.relationships.field_divider_secondary_image.localFile) || null;
        solo = 2;
    }



    return (
        <StyledDivider background={bg} solo={solo} reverse={node.field_reverse}>
            { primeImg != null &&
                <DividerImg alt={altPrime} solo={solo} className='primary' image={primeImg} />
            }
            { secImg != null &&
                <DividerImg alt={altSec} solo={solo} className='secondary' image={secImg} />
            }
            { terImg != null &&
                <DividerImg alt={altTer} solo={solo} className='tertiary' image={terImg} />
            }
        </StyledDivider>
    )
}

export default Divider;

export const fragment = graphql`
    fragment Divider on paragraph__divider{
        id
        field_background_color
        field_reverse
        primary_img: field_divider_main_image{
            alt
        }
        secondary_img: field_divider_secondary_image{
            alt
        }
        tertiary_img: field_divider_tertiary_image{
            alt
        }
        relationships {
            field_divider_main_image {
                id
                localFile {
                    childImageSharp {
                        gatsbyImageData(
                        placeholder: BLURRED
                        width: 726
                        height: 400
                        layout: CONSTRAINED
                        )
                    }
                }
            }
            field_divider_secondary_image {
                id
                localFile {
                    childImageSharp {
                        gatsbyImageData(
                        placeholder: BLURRED
                        width: 348
                        height: 400
                        layout: CONSTRAINED
                        )
                    }
                }
            }
            field_divider_tertiary_image {
                id
                localFile {
                    childImageSharp {
                        gatsbyImageData(
                        placeholder: BLURRED
                        width: 348
                        height: 280
                        layout: CONSTRAINED
                        )
                    }
                }
            }
        }
    }
`;