import React from "react";
import { graphql } from "gatsby";

const HtmlField = ({ node }) => {

  return (
    <>
      {node.htmlCont != null &&
        <div dangerouslySetInnerHTML={{ __html: node.htmlCont.processed }} />
      }
    </>
  )
}

export default HtmlField;

export const fragment = graphql`
  fragment HtmlField on paragraph__html_field {
      id
      htmlCont: field_html {
          processed
      }
  }
`;
