import React from "react";
import styled from "styled-components";
import { graphql } from "gatsby";
import breakpoint from '../styles/breakpoints';

const StatContainer = styled.div`
	position: relative;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	column-gap: 32px;
	justify-content: center;
	padding: 80px 0;
	
	@media screen and ${breakpoint.device.sm}{
        padding-top: 60px;
        padding-bottom: 60px;
    }
	
	@media screen and ${breakpoint.device.xs}{
        padding-top: 45px;
        padding-bottom: 45px;
    }
	@media screen and ${breakpoint.device.xs}{
		display: block;
		column-gap: none;
	}
`
const Stat = styled.div`
	position: relative;
	display: block;
	text-align: center;
	width: 100%;
	max-width: 348px;
	margin: 0 auto;
	@media screen and ${breakpoint.device.xs}{
		margin-bottom: 1.5rem;
		&:last-child{
			margin-bottom:0;
		}
	}
	h2,h3,p{
		line-height: 1.25em;
		margin: 0;
		padding: 0;
		text-align: center;
	}
`
const StatsDivider = ({ node }) => {
	return (
		<StatContainer>
			{node.field_stat.map((stat, i) => (
				<Stat key={i++} dangerouslySetInnerHTML={{ __html: stat.processed }} />
			))}
		</StatContainer>
	)
}

export default StatsDivider;

export const fragment = graphql`
	fragment StatsDivider on paragraph__stats_divider {
		id
		field_stat {
			processed
		}
	}
`;
