import React from "react";
import AppendHead from 'react-append-head';
const GiggBoard = ({ accountId, folderId, displayType = "boardOne" }) => {
    return (
        <>
            <AppendHead>
                <script src="https://my.gigg.com/bragify.min.js" order={0} />
                <script type="text/javascript" order={1}>
                    {`window.bragify.fanBoard({
                        accountId: '${accountId}',
                        folderId: '${folderId}',
                        displayType: '${displayType}',
                        domElementId: 'bragify-feed'
                    })`}
                </script>
            </AppendHead>
            <div id="bragify-feed"></div>
        </>
    );
};
export default GiggBoard;