import React from 'react';
import { graphql } from 'gatsby';
import UAColors from 'ua-color-name';
import styled from 'styled-components/macro';
import breakpoint from '../styles/breakpoints';

const TableWrap = styled.section`
	position: relative;
	display: block;
	padding-top: ${({ above }) => above};
	padding-bottom: ${({ below }) => below};

	@media screen and ${breakpoint.device.sm}{
        padding-top: 60px;
        padding-bottom: 60px;
    }
	@media screen and ${breakpoint.device.xs}{
        padding-top: 45px;
        padding-bottom: 45px;
    }
`;

const TableContainer = styled.div`
	position: relative;
	display: block;
`;

const Table = styled.table`
	width: 100%;
	position: relative;
	text-align: left;
	color: ${UAColors('azurite')};
	line-height: 1.8em;
	tr:first-child{
		td{
			border-top: 1px solid ${UAColors('silver')};
		}
	}
	tr{
		td:first-child{
			font-weight: 600;
			border-left: 1px solid ${UAColors('silver')};
		}
		td{
			border-bottom: 1px solid ${UAColors('silver')};
			padding: 8px 15px;
			span.closed{
				color: ${UAColors('silver')};
			}
		}
		th{
			color: ${UAColors('arizonablue')};
			font-weight: 700;
			padding-bottom: 0.4em;
		}
		td:last-child{
			border-right: 1px solid ${UAColors('silver')};
		}
	}

`

const DeadlinesTable = ({ node }) => {
	let deadLines = node.relationships.deadline,
			columns = node.columns;

	// If Application Status is a column create element for each deadline object
	if(columns && columns.includes('Application_Status')){
		deadLines.forEach(function(deadLine) {
			let appStatus = new Date(deadLine['Application_Deadline']) >= new Date().setHours(0, 0, 0, 0) ? 'Accepting Applications' : 'Closed';
			deadLine['Application_Status'] = appStatus;
		})
	}

	//Sort Array of Deadlines
	if(deadLines) {
		deadLines.sort(function(a,b) {
			return (a.Application_Deadline < b.Application_Deadline) ? 1 : ((b.Application_Deadline < a.Application_Deadline) ? -1 : 0);
		});
    }
	return (
		<TableWrap above={node.field_top_padding} below={node.field_bottom_padding}>
			<TableContainer>
				<Table cellSpacing="0">
					{node.showColHeadings ?
						(
							<thead>
								<tr>
									{node.columns.map((col, i) =>
										<th key={i}>{col.replace('_', ' ')}</th>
									)}
								</tr>
							</thead>
						) : null}
					<tbody>
						{deadLines.map((deadLine, i) =>
							<tr key={i}>
								{columns.map((col, j) =>
									<td key={j+i++} className={col.toLowerCase().replace('_','')}>
										{
											(col === 'Application_Status' && deadLine[col] === 'Closed') ?
											<span className="closed">{deadLine[col]}</span> : deadLine[col]
										}
									</td>
								)}
							</tr>
						)}
					</tbody>
				</Table>
			</TableContainer>
		</TableWrap>
	)
}

export default DeadlinesTable;

export const fragment = graphql`
  fragment DeadlinesTable on paragraph__deadline_table{
	field_bottom_padding
	columns: field_column_headings
	showColHeadings: field_display_column_headings
	field_top_padding
	relationships {
		deadline: field_deadline_item {
		  ... on node__admissions_deadline {
		  Title: title
		  Display_Title: field_display_title
		  Starting_Semester: field_semester
		  Application_Open: field_application_open(formatString: "MMMM D, YYYY")
		  Application_Deadline: field_application_deadline(formatString: "MMMM D, YYYY")
		  }
	  }
	}
  }
`