import React from "react";
import UAColors from 'ua-color-name';
import styled from "styled-components";
import { getSection } from "../paragraphHelpers";
import { graphql } from "gatsby";
import breakpoint from '../styles/breakpoints';

const Column = styled.div`
	position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
	width: 100%;
	max-width: 348px;
	h2{
		text-align: center;
		color: ${UAColors('arizonablue')}
	}

	@media screen and ${breakpoint.device.xs}{
		margin: auto;
    }
`

const ButtonColumn = ({ node }) => {
	let bttnCol = node.relationships.paragraphs.map(getSection),
		colHead = node.field_col_head;
	return (
		<Column>
			{colHead !== null ? (<h2>{colHead.value}</h2>) : ''}
			{bttnCol}
		</Column>
  	)
}
export default ButtonColumn;

export const fragment = graphql`
	fragment ButtonColumn on paragraph__button_column {
		id
		relationships {
			paragraphs: field_bttns {
				type: __typename
				...Button
			}
		}
		field_col_head {
			value
		}
	}
`;
