import React from 'react';
import styled, { css } from 'styled-components/macro';
import { graphql } from "gatsby";
import BackgroundImage from 'gatsby-background-image'
import breakpoint from '../styles/breakpoints';
import { getSection } from "../paragraphHelpers";
import { Link } from ".";
import chevron from '../assets/images/chevron-circle.svg';
import whiteChevron from '../assets/images/small-cheveron-white.svg'
import breakpoints from '../styles/breakpoints';
import Menu from './Menu';


const Hero = styled.div`
    display:  block;
    position: relative;
    z-index: 0;

    ${({ landing }) =>
    landing &&
    css`
        display: flex;
        align-items: center;
        justify-content: space-between;
    `}

    width: 100%;
    max-width: 1180px;

    padding: ${({ landing }) => (landing ? '302px 24px' : '114px 24px')};
    margin: auto;
    box-sizing: border-box;

    .button{
        border: 2px solid var(--bloom);
        max-width: 248px;

        &:hover{
            background-color: var(--bloom);
        }
    }

    @media screen and ${breakpoint.device.md}{
        padding: 114px 24px;
    }

    @media screen and ${breakpoint.device.sm}{
        padding: 75px 24px;
    }

    @media screen and ${breakpoint.device.xs}{
        padding: 60px 24px;
        margin-top: 105px;
        ${({ hasMenu }) =>
        hasMenu &&
        css`
            > ${HeroGroup}{
                max-width: 100%;
            }
        `}
    }
`;


const HeroContainer = styled(BackgroundImage)`
    width: 100%;
    background-position: center;
    background-repeat: none;
    background-size: cover;

    z-index: 900;
`;


const HeroGroup = styled.div`
    display: flex;
    flex-direction: ${({ direction }) => direction};
    align-items: ${({ align }) => align};
    justify-content: ${({ justify }) => justify};

    width: 100%;

    &.btn-scroll-warp{
        @media screen and (max-width: 815px){
            flex-direction: column;
            align-items: flex-start;
        }
        @media screen and ${breakpoints.device.xs}{
            align-items: center;
        }
    }

    @media screen and ${breakpoints.device.xs}{
        .button{
            display: block;
            margin: 24px auto 0;
        }
        ${({ direction }) =>
        direction === 'row' &&
        css`
            flex-direction: column;
        `}
    }
`;

const HeroScroll = styled.button`
    display: flex;
    font-family: Proxima Nova Condensed;
    margin-top: ${({ margintop }) => margintop};
    color: white;
    background: none;
		font-weight: 700;
    border: none;
    font-size: 20px;
		font-style: italic;
		text-align: left;
    &:before{
        content: '';

        display: inline-block;

        width: 47px;
        height: 47px;

        margin-right: 15px;
        margin-left: 15px;

        transform: rotate(90deg);

        background-image: url(${chevron});
        background-size: contain;
        background-repeat: no-repeat;
    }

    @media screen and (max-width: 815px){
        margin-top: 24px;
    }

    @media screen and ${breakpoint.device.xs}{
        margin-top: 24px;
        flex-direction: column;
        text-align: center;
        justify-content: center;

        &:before{
            width: 35px;
            height: 35px;
            margin: 0 auto 24px;
        }
    }
`;


const HeroTitle = styled.h1`
    display: block;
    position: relative;

    color: white;
    font-size: 21px;
    font-weight: 900;
    text-transform: capitalize;
    line-height: 1;

    &:before{
        content: "";

        position: absolute;
        top: -24px;
        left: 0;

        width: 27px;
        height: 7px;

        background-color: var(--bloom);

        @media screen and ${breakpoint.device.xs}{
        right: 0;
        margin:auto;
        }
    }

    @media screen and ${breakpoint.device.xs}{
        text-align: center;
    }
`;

const HeroCTA = styled.strong`
    p, span{
        display: block;
        color: white;
        font-weight: 900;
        font-size: 50px;
        text-transform: uppercase;
        line-height: 1;
        font-size: var(--h1-internal-font-size);

        ${({ landing }) =>
        landing &&
        css`
            font-size: 78px;
        `}

        @media screen and ${breakpoint.device.md}{
            ${({ landing }) =>
            landing &&
            css`
                font-size: 68px;
            `}
        }
        @media screen and ${breakpoint.device.sm}{
            ${({ landing }) =>
            landing &&
            css`
                font-size: 58px;
            `}
        }
        @media screen and ${breakpoint.device.xs}{
            margin: auto;
            text-align: center;
            ${({ landing }) =>
            landing &&
            css`
                font-size: 48px;
            `}
        }
    }
`;

const Breadcrumbs = styled.ul`
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;

    &:before{
        content: "";

        position: absolute;
        top: -24px;
        left: 0;

        width: 27px;
        height: 7px;

        background-color: var(--bloom);

        @media screen and ${breakpoint.device.xs}{
        right: 0;
        margin:auto;
        }
    }

    @media screen and ${breakpoint.device.xs}{
        justify-content: center;
    }
`;

const Crumb = styled.li`
    a{
        border: none !important;
        background-color: transparent;
        font-weight: 700;
        text-decoration: underline;
        /* transition: font-weight: */

        &.button:hover{
            background-color: transparent;
            /* font-weight: 900; */
        }
    }

    > span{
        display: block;
        color: white;
        position: relative;

        padding-left: 24px;

        &:before{
            content: '';
            background-image: url(${whiteChevron});
            width: 7px;
            height: 14px;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 9px;
            margin: auto;
            transform: rotate(180deg);
        }
    }

    @media screen and ${breakpoint.device.sm}{
        a.button{
            margin: 0;
        }
    }
`;


const ParagraphHero = (props) => {
    let node = props.node,
				relations = node.relationships,
				currPage = relations.paragraph__section[0].relationships.node__admissions_flexible_page[0],
				bgImg = relations.field_hero_background_image || null,
				menuPages = currPage.relationships.field_admissions_audience[0] || null,
				audience = 'main',
				breadcrumb = null,
				pageTitle = currPage.title,
				cta = node.hero_cta || null,
				landing = currPage.field_landing_page,
				btn = null,
				top = '0px';

    if(menuPages != null) {
        audience = menuPages.relationships.node__admissions_flexible_page[0].relationships.field_admissions_audience[0].name || null;
        menuPages.relationships.node__admissions_flexible_page.forEach(function(pg) {
            if(pg.field_landing_page){
                breadcrumb = pg.path.alias
            }
        });

        if(menuPages.relationships.node__admissions_flexible_page != null){
        }
    }

    if(relations.heroButton){
        btn = getSection(relations.heroButton);
        if (btn.props.node.field_margin_top != null) {
            top = btn.props.node.field_margin_top;
        }
    }

		if(bgImg != null){
			bgImg = bgImg.localFile.childImageSharp.fluid;
		}

		const scroll = () => {
			let cur = document.querySelector('#hero'),
					next = cur.nextElementSibling;
			if(next != null){
				next.scrollIntoView({ behavior: 'smooth', block: 'start' });
			}
		}

    return (
        <>
            { node.field_has_menu &&
                <Menu mobile={true} landing={landing} menuName={audience} />
            }
            <HeroContainer Tag="section" fluid={bgImg}>
                <Hero>
                    <HeroGroup direction="column" align='flex-start' justify='space-between'>
                        <HeroGroup direction="column">
                            {landing &&
                                <HeroTitle>{node.field_headline_one}</HeroTitle>
                            }
                            {!landing && breadcrumb != null &&
                                <Breadcrumbs>
                                    <Crumb>
                                        <Link menu={false} primary="primary" margintop="0px" marginbottom="0px" to={breadcrumb}><span>{audience.replace(" ", "-") + ' Students'}</span></Link>
                                    </Crumb>
                                    <Crumb>
                                        <span>{pageTitle}</span>
                                    </Crumb>
                                </Breadcrumbs>
                            }

                            {cta != null &&
                                <HeroCTA landing={landing} dangerouslySetInnerHTML={{ __html: cta.processed }} />
                            }
                        </HeroGroup>
                        {landing &&
                            <HeroGroup className='btn-scroll-warp' direction="row" align="center" justify='flex-start'>
                                {btn != null &&
                                    btn
                                }
                                <HeroScroll margintop={top} onClick={scroll} chevron={chevron}>Click or Scroll<br />To Explore</HeroScroll>
                            </HeroGroup>
                        }
                    </HeroGroup>
                    { node.field_has_menu &&
                        <Menu mobile={false} landing={landing} menuName={audience} />
                    }
                </Hero>
            </HeroContainer>
        </>
    );
};

export default ParagraphHero;

export const fragment = graphql`
    fragment Hero on paragraph__admissions_hero{
        id
        field_headline_one
        field_has_menu
        hero_cta: field_headline_two{
            format
            processed
            value
        }
        field_hero_background_image {
            alt
        }
        relationships {
            paragraph__section {
                relationships {
                    node__admissions_flexible_page {
                        id
                        field_landing_page
                            title
                        relationships {
                            field_admissions_audience {
                                relationships {
                                    node__admissions_flexible_page {
                                        id
                                        title
                                        field_landing_page
                                        path {
                                        alias
                                        }
                                relationships {
                                field_admissions_audience {
                                    name
                                }
                                }
                            }
                            }
                        }
                        }
                    }
                    }
                }
            heroButton: field_cta{
				type: __typename
				...Button
			}
            field_hero_background_image {
                localFile {
                    childImageSharp {
                        fluid(quality: 90, maxWidth: 4000){
                            ...GatsbyImageSharpFluid
                        }
                    }
                }
            }
        }
    }
`;
