import React from 'react';
import styled, { css } from 'styled-components/macro';
import { graphql, useStaticQuery } from 'gatsby';
import breakpoint from '../styles/breakpoints';
import UAColors from 'ua-color-name';
import blueChevron from '../assets/images/small-cheveron-blue.svg'
import whiteChevron from '../assets/images/small-cheveron-white.svg'
import { Link } from ".";

// ANCHOR: Styles
const StyledMenu = styled.ul`
    background: white;
    width: 285px;
    position: absolute;
    right: 24px;
    top: 114px;
    @media screen and ${breakpoint.device.xs}{
        display: none;
    }
    ${({ landing }) =>
    landing &&
    css`
        transform: translateY(-50%);
        top: 50%;
        li{
            &:first-of-type{
                &:before{
                    display: none !important;
                }
                span{
                    color: white;
                    font-size: 18px;
                    display: block;
                    padding: 15px 12px 6px 12px;
                    font-weight: 700;
                }
            }
        }
    `}
    li{
        a{
            display: block;
            &.button{
                border: none;
                max-width: initial;
                color: var(--arizona-blue);
            }
            span{
                display: inline-block;
                font-weight: 700;
                font-size: 18px;
                text-transform: capitalize;
                transform: scale(1);
                transition: transform .3s ease-in-out;
                transform-origin: left;
            }
            background: none;
            text-align: left;
            padding: 15px 12px 6px 12px;

            position: relative;
            &:after{
                content: '';
                width: 100%;
                height: 1px;
                background-color: var(--silver);
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto;

                transition: height .3s ease-in-out, background-color .3s ease-in;
            }

            border-radius: 0;

            &:hover{
                transform: none;
                background-color: initial;
                border-width: 2px;
                border-color: var(--arizona-blue);
                &.button{
                    background-color: initial;
                }
                span{
                    transform: scale(1.1);
                    &:after{
                        transform:rotate(180deg) translateX(0px) !important;
                        opacity: 1 !important;
                    }
                }
            }
        }

        &:first-child{
            background-color: ${UAColors('uared')};
            position: relative;
            display: flex;
            align-items: center;
            a{
                &:after{
                    display: none;
                }
            }
            &:before{
                content: '';

                display: inline-block;

                width: 9px;
                height: 14px;
;
                margin-left: 15px;
                margin-top: 9px;

                background-image: url(${whiteChevron});
                background-size: contain;
                background-repeat: no-repeat;

                transform: scale(1);
                transition: transform .3s ease-in-out;
                transform-origin: right;
            }
            &:hover{
                &:before{
                    transform: scale(1.1);
                }
            }
            a{
                /* pointer-events: none; */
                span{
                    color: white;
                }
            }
        }
        &:last-child{
            a{
                &:after{
                    display: none;
                }
            }
        }
        &:not(:first-child){
            a{
                span{
                    position: relative;
                    &:after{
                        content: '';

                        position: absolute;;
                        top: 0;
                        bottom: 0;
                        left: calc(100% + 24px);

                        margin: auto;

                        width: 13px;

                        background-image: url(${blueChevron});
                        background-size: contain;
                        background-repeat: no-repeat;

                        transform:rotate(180deg) translateX(20px);
                        opacity: 0;

                        transition: transform .3s ease-in-out, opacity .3s ease-in-out;
                    }
                }
            }

        }
    }
    /* Mobile menu styles */
    ${({ mobile }) =>
    mobile &&
        css`
            transform: translateY(0);
            position: relative;
            top: 0;
            right: 0;
            display: none;
            @media screen and ${breakpoint.device.xs}{
                display: block;
                width: 100%;
            }
    `}
`;

const MobileMenu = styled.button`
    display: block;
    position: relative;
    display: flex;
    align-items: flex-start;
    border: none;
    border-bottom: 1px solid ${UAColors('arizonablue')};
    padding-top: 24px;
    padding-bottom: 9px;
    margin: 0 auto;
    background-color: transparent;
    width: calc(100% - 96px);
    color: ${UAColors('arizonablue')};
    display: none;

    .span{
        pointer-events: none;
    }

    + ${StyledMenu}{
        display: none;
        padding: 17px;
        margin: 0 auto;
        width: calc(100% - 96px);
        max-height: 0px;
        overflow: hidden;
        transition: max-height .3s ease-in-out;
        li:first-child{
            background-color: transparent;
            a.button span{
                color: ${UAColors('arizonablue')};
                padding-left: 0;
                background-color: white;
            }
            a.button{
                background-color: white;
            }
            &:before{
                display: none;
            }
        }

        li{
            background-color: white;
            border-bottom: 1px solid ${UAColors('arizonablue')};
        }
        &.opened{
            max-height: 600px;
            z-index: 900;
        }
    }

    .title{
        position: relative;
        font-size: 24px;
        font-weight: 900;
        padding-left: 15px;
    }

    &.opened{
        .hamburger{
            background-color: white;
            transition-delay: 0.2s
            transition-duration: 0s;
            &:before, &:after{
                transition-delay: 0s, 0.2s;
            }
            &:before{
                margin-top: 0;
                transform: rotate(45deg);
            }
            &:after{
                margin-top: 0;
                transform: rotate(-45deg);
            }
        }
    }

    .hamburger {
        position: relative;
        top: 12px;
        width: 30px;
        height: 6px;
        background-color: ${UAColors('arizonablue')};
        display: block;

        &:before, &:after {
            position: absolute;
            content: '';
            width: 30px;
            height: 6px;
            background-color: ${UAColors('arizonablue')};
            display: block;
            transition-property: margin, transform;
            transition-duration: 0.2s;
            transition-delay: 0.2s, 0s;
        }
        &:before {
            margin-top: -12px;
        }
        &:after {
            margin-top: 12px;
        }
    }
    @media screen and ${breakpoint.device.xs}{
        display: flex;
        + ${StyledMenu}{
            display: block;
        }
    }
`;

//ANCHOR: Functions
const click = (e) => {
    let self = e.target;
    let menuItems = document.getElementById('menu-items');
    if(self.classList.contains('closed')){
        self.classList.remove('closed');
        menuItems.classList.remove('closed');
        self.classList.add('opened');
        menuItems.classList.add('opened');
    } else{
        menuItems.classList.remove('opened');
        self.classList.remove('opened');
        self.classList.add('closed');
        menuItems.classList.add('closed');
    }
}

const getMenu = (allMenus, menuName) => {
    let menu = null;
    allMenus.forEach(function(element) {
        if(element.menu_name === menuName && element.parent == null){
            menu = element;
        }

    });

    return menu;
}

const getMenuName = (menuName) => {
    let name = menuName.replace(" ", "-").toLowerCase();
    name = name + '-menu';

    return name;
}

const menuItemsUnite = (menuItems, landing, isMobile) => {
    let parent = null;
    let menu = null;
    if(menuItems != null){
        if(landing){
            parent = <li><span>{menuItems.title + ' Menu'}</span></li>;
        }else{
            parent = <li><Link menu={true} primary="primary" margintop="0px" marginbottom="0px" to={menuItems.url}><span>{menuItems.title + ' Home'}</span></Link></li>;
        }
        const childrenItems = menuItems.childrenMenuItems;
        const children = childrenItems.map((page, index) => {
            return(
                <li key={index}>
                    <Link menu={true} primary="primary" margintop="0px" marginbottom="0px" to={page.url}><span>{page.title}</span></Link>
                </li>
            );
        });

        menu = <>
            {parent}
            {children}
        </>;
    }

    return menu;
}

const menuZone = (allMenus, menuName, isMobile, landing) => {
    const name = getMenuName(menuName, landing);
    const menuItems = getMenu(allMenus, name);
    const menu = menuItemsUnite(menuItems, landing, isMobile);

    return menu;
}

const Menu = ({landing, menuName, mobile}) => {

    const allMenusQuery = useStaticQuery(graphql`{
        allMenuItems(sort: {fields: weight, order: ASC}){
            nodes{
                menu_name
                title
                url
                childrenMenuItems {
                    url
                    weight
                    title
                }
                parent{
                    id
                }
            }
        }
    }`);

    const allMenus = allMenusQuery.allMenuItems.nodes

    if(mobile){
        return(
                <div id='mobile-menu-wrap'>
                    <MobileMenu className='closed' onClick={click}>
                        <span className='hamburger'></span>
                        <span className='title'>{menuName.replace(" ", "-") + ' Menu'}</span>
                    </MobileMenu>
                    <StyledMenu id='menu-items' className='closed' landing={landing} mobile={mobile}>
                        {menuZone(allMenus, menuName, landing)}
                    </StyledMenu>
                </div>
        )
    }

    return(
        <StyledMenu landing={landing} mobile={mobile}>
            {menuZone(allMenus, menuName, mobile, landing)}
        </StyledMenu>
    )
}

export default Menu;